import React from "react";
import Layout from "../components/layout";
import Seo from "../components/seo";
import BreadCrumb from "../components/common/breadcrumb";
import DecentralizedExchangeScreen from "../screens/decentralize-exchange";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";

const DecentralizeExchangeDevelopmentPage = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Decentralized Exchange Development", link: null },
  ];
  return (
    <Layout db={db}>
      <Seo title="Decentralized Exchange Development" />
      <BreadCrumb items={breadcrumbLinks} />
      <DecentralizedExchangeScreen />
    </Layout>
  );
};

export default DecentralizeExchangeDevelopmentPage;
